@include tablet{
    .contendor-anuncios{
        @include grid (3,2rem)
    }
}
.anuncio{
    background-color: lighten($gris, 5);
    border: 1px solid $grisborder;
    margin-bottom: 2rem;

    .contendor-anuncios{
        padding: 2rem;

    }
    h3,p{
        margin: 0;
    }

}
.precio{
    color: $verde;
    font-weight: $bold;
    font-size: 3rem;
}
.iconos-caracteristicas{
    display: flex;
    list-style: none;
    padding: 0;
    max-width: 40rem;
    li{
        flex: 1;
        display: flex;
        img{
            flex: 0;
            margin-left: 2rem;
        }
        p{
            font-weight: $black;
        }
    }

}