.inicio{

    background-image: url(../img/header.jpg);
}
.wepb .inicio{
    background-image: url(../img/header.wepb);
    
}

.header{
    background-color: $grisOscuro;
    padding: 1rem 0 3rem 0;
    &.inicio {
        background-position: center center;
        background-size: cover;
        background-repeat: no-repeat;

        .contenido-header {
            @include tablet {
                min-height: 70rem;
            }
        }
    }
}
.contenido-header { 
    display: flex;
    flex-direction: column; // Una vez aplicado flex culumn se puede ajustar de manera vertical con justify-content
    justify-content: space-between;
    //Este include permite ajustar al header el tamaño para bajar el h1
    h1{
        
        text-align: left;
        color: $blanco;
        font-weight: $bold;
        line-height: 2;
        max-width: 60rem;
    }
}
