.formulario {
    p{
        font-size: 1.4rem;
        color: $grisOscuro;
        margin: 2rem 0 0;
    }
    legend{
        font-size: 2rem;
        color: $grisOscuro;
    }
    label{
        font-weight: $bold;
        text-transform: uppercase;
        display: block;
    }

    // La funcio de not junto con el selector de actributos permite selecionar todo excepto el que le indiques
    input:not([type="submit"]), 
    textarea, 
    select{
        padding: 1rem;
        display: block;
        width: 100%;
        border: 1px solid $gris;
        border-radius: 1rem;
        margin-bottom: 2rem;
    }
    textarea{
        height: 20rem;
    }
    .forma-contacto{
        max-width: 20rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    // Se usa el selector de atributos
    input[type="radio"]{
        margin: 0;
        width: auto;
    }
}
