.testimonial {
    background-color: $verde;
    color: $blanco;
    border-radius: 2.5rem;
    font-size: 2.4rem;
    text-align: left;
    padding: 2rem;
    blockquote{
        padding-left: $separacion;
        position: relative;
        
        &::before {
            content: '';
            background-image: url(../img/comilla.svg);
            background-repeat: no-repeat;
            width: 6rem;
            height: 8rem;
            display: block;
            position: absolute;
            left: -2rem;
            
        }
    }
    p{
        text-align: right;
        color: $blanco;
    }
}